@import "variables";

@media screen and (max-width: 1500px), screen and (max-device-width: 1500px){
  body{
    .contentAddres {
      iframe {
        height: 465px;
      }
      .contentAddres__contactForm {
        .comentary {
          input[type*="text"] {
            width: 184px;
          }
          textarea {
            width: 395px;
          }
        }
        .contacts,
        .social{
          width: 75%;
        }
      }
    }
  }
}

@media screen and (max-width: 1310px), screen and (max-device-width: 1310px){
  body{
    .contentAddres {
      .contentAddres__contactForm {
        .contacts,
        .social{
          width: 80%;
        }
      }
    }
  }
}

@media screen and (max-width: 1190px), screen and (max-device-width: 1190px){
  body{
    .contentAddres {
      .contentAddres__contactForm {
        .comentary {
          margin: 25px 0 0 25px;
          input[type*="text"] {
            width: 144px;
          }
          textarea {
            width: 313px;
          }
        }
        .contacts,
        .social{
          margin: 0 0 0 25px;
        }
      }
    }
  }
}

@media screen and (max-width: 940px), screen and (max-device-width: 940px){
  body{
    .contentAddres {
      .contentAddres__contactForm {
        .comentary {
          input[type*="text"] {
            width: 120px;
            height: 30px;
          }
          textarea {
            width: 266px;
          }
        }
      }
    }
  }
}

@media screen and (max-width: 870px), screen and (max-device-width: 870px){
  body{
    .contentAddres {
      .contentAddres__contactForm {
        .comentary {
          display: flex;
          flex-direction: column;
          input[type*="text"], textarea {
            width: 100%;
          }
        }
      }
    }
  }
}

@media screen and (max-width: 870px), screen and (max-device-width: 870px){
  body{
    .contentAddres{
      flex-direction: column;
      iframe{
        width: 100%;
      }
      &__contactForm{
        width: 100%;
        .comentary{
          margin: 25px 8% 0!important;
          align-items: center;
          width: 90%;
          button{
            margin: 0;
            width: 80%;
          }
        }
        .contacts{
          margin: 25px 8% 0!important;
          align-items: center;
          width: 90% !important;
        }
        .social{
          margin: 25px 8%!important;
          align-items: center;
          width: 90% !important;
        }
      }
    }
  }
}